<template>
  <div class="nav num-nav">
   <el-row class="tac">
    <el-col :span="24">
      <el-menu
        :default-active="navItem"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        :router=false
        @close="handleClose">
        <el-submenu index="1">
          <template slot="title">
            <i><img src="static/images/number_asset/number_asset.png" alt="" class="nav-icon"></i>
            <!-- <i><img :src="item.image_url" alt="" class="nav-icon"></i> -->
            <!-- <img src="../../images/number_asset/number_asset.png"> -->
            <span>{{ $t("navBread.numberAsset") }}</span>
          </template>
          <el-menu-item-group>
            <el-menu-item :index="list.currency_name" v-for="list in navObj" :key="list.id" @click="$emit('getGuideContent', list.introduce, list.currency_name)">{{ list.currency_name }}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-col>
  </el-row>
  </div>
</template>
<script>
  export default {
    props: ['navItem', 'navObj'],
    methods: {
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    }
  }
</script>

<style lang="less">
  .num-nav{
    .nav-icon{
      width: 20px;
      height: 20px;
    }
  }
</style>



