<template>
  <div class="guide container">
    <div class="nav-bread">{{ $t("navBread.des") }}: > <router-link to="/"> {{ $t("navBread.index") }} </router-link> > <router-link to="/"> {{ $t("navBread.fabiTrade") }} </router-link>> <a @click="handleNumberAssetRouter" href="javascript:void(0)"> {{ $t("navBread.numberAsset") }} </a> > <router-link :to="navItem"> {{ navName }} </router-link></div>
    <div class="clearfix">
      <div class="navleft pull-left">
        <navleft :navItem="navItem" :navObj="navObj" @getGuideContent="getGuideContent"></navleft>
      </div>
      <div class="r-content pull-right" >
        <div id="guide-title" class="guide-title clearfix" >
					<div class="pull-left" v-html="navName"></div>
          <div class="pull-right">
            <el-button type="primary" @click="handleTrade">{{ $t("numberAsset.toTrade") }}</el-button>
          </div>
				</div>
         <div id="guide-content" v-html="content">
         	
         </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { getAllAsset } from '@/api/other'
  import navleft from './components/leftNavChange'
  export default {
    components: { navleft },
    data() {
      return {
        navItem: '',
        navName: '',
        navObj: [],
        title: '',
        content: '',
        id: ''
      }
    },
    created() {
      this.stateChange()
      this.init()
    },
    methods: {
      stateChange() {
  
      },
      handleNumberAssetRouter() {
        window.location.reload()
      },
      init() {
        getAllAsset().then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.navObj = [...res.data]
          if (this.navObj.length > 0) {
            this.navItem = res.data[0].currency_name
            this.navName = res.data[0].currency_name
            this.getGuideContent(this.navObj[0].introduce, this.navName)
          }
        }).catch(() => {
  
        })
      },
      getGuideContent(introduce, name) {
        this.navName = name
        this.navItem = name
        this.content = introduce
      },
      handleTrade() {
        this.$router.push('/trade')
      }
    }
  }
</script>
<style lang="less" scoped>
  .r-content{
    background: white;
    min-height: 770px;
    padding: 20px 25px;
    width: 917px !important;
  }
  .guide-title{
      padding-top: 9px;
      padding-bottom: 18px;
      border-bottom: 1px solid #e6eaf0;
      font-size: 16px;
  }
  .content img{
    width: 100%;
    height: auto;
    border: 1px solid #F5F5F5;
  }
  .content h5{
    padding: 20px 0;
    color: #787878;
  }
  #guide-content{
    padding: 20px;
  }

</style>


